$('.beforeAfter').beforeAfter({

    // is draggable/swipeable
    movable: true,

    // click image to move the slider
    clickMove: true,

    // always show the slider
    alwaysShow: true,

    // intial position of the slider
    position: 50,

    // opacity between 0 and 1
    opacity: 1,
    activeOpacity: 1,
    hoverOpacity: 1,

    // slider colors
    separatorColor: '#1E2630',
    bulletColor: '#ffffff',
    arrowColor: '#333333',

});


$(".qa-card__show").click(function () {
    $(this).parents(".qa-card").toggleClass("_active")
    $(this).siblings(".qa-card__hidden").slideToggle()
})
