function casesSlider() {
    var swiper = new Swiper('.cases .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        watchSlidesProgress: true,
        preventClicks :true,
        a11y: false,
        noSwipingSelector: ".beforeAfter__btn,.beforeAfter__separator",
        navigation: {
            nextEl: '.cases .swiper-button-next',
            prevEl: '.cases .swiper-button-prev',
        },
        pagination: {
            el: '.cases .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

